import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../App.css"; // Import CSS

const CreateQuestionPaper = () => {
  const [questionText, setQuestionText] = useState('');
  const [answerType, setAnswerType] = useState('MCQ');
  const [choices, setChoices] = useState([]);
  const [schoolName, setSchoolName] = useState(''); // New state for school name
  const [questions, setQuestions] = useState([]);
  const navigate = useNavigate();

  const addQuestion = () => {
    setQuestions([...questions, { questionText, answerType, choices }]);
    setQuestionText('');
    setAnswerType('MCQ');
    setChoices([]);
  };


  const submitPaper = () => {
    localStorage.setItem('questions', JSON.stringify(questions));
    localStorage.setItem('schoolName', schoolName); // Save school name
    navigate('/attend');
  };

  const handleChoiceChange = (index, value) => {
    const newChoices = [...choices];
    newChoices[index] = value;
    setChoices(newChoices);
  };

  const deleteQuestion = (index) => {
    setQuestions(questions.filter((_, i) => i !== index));
  };

  const deleteAllQuestions = () => {
    setQuestions([]);
  };

  return (
    <div>
      <div className="navBar">
        
        <button className="danger" onClick={deleteAllQuestions}>
          All delete
        </button>
        <button className="sbmit" onClick={submitPaper}>
          Submit
        </button>
      </div>
      <div className="container">
        <h1>Create Question Paper</h1>
      <h2>school Name</h2>
        <input
        type="text"
        placeholder="Enter school name"
        value={schoolName}
        onChange={(e) => setSchoolName(e.target.value)}
      />
        <h2>Questions</h2>
        <div className="added-questions">
          {questions.map((question, index) => (
            <div key={index} className="question-box">
              <p>
                <strong>Question {index + 1}:</strong> {question.questionText}
              </p>
              {question.answerType === "MCQ" ? (
                <ul>
                  {question.choices.map((choice, i) => (
                    <li key={i}>{choice}</li>
                  ))}
                </ul>
              ) : (
                <p>
                  <em>Theory Answer</em>
                </p>
              )}
              <button className="danger" onClick={() => deleteQuestion(index)}>
                Delete Question
              </button>
            </div>
          ))}
        </div>
        <input
          type="text"
          placeholder="Enter Question"
          value={questionText}
          onChange={(e) => setQuestionText(e.target.value)}
        />

        <select
          value={answerType}
          onChange={(e) => setAnswerType(e.target.value)}
          className="anstypes"
        >
          <option value="MCQ">MCQ</option>
          <option value="Theory">Theory</option>
        </select>

        {answerType === "MCQ" && (
          <div>
            {choices.map((choice, index) => (
              <input
                key={index}
                type="text"
                placeholder={`Choice ${index + 1}`}
                value={choice}
                onChange={(e) => handleChoiceChange(index, e.target.value)}
              />
            ))}
            <button
              className="secondary"
              onClick={() => setChoices([...choices, ""])}
            >
              Add Choice
            </button>
          </div>
        )}

        <button onClick={addQuestion}>Add Question</button>
      </div>
    </div>
  );
};

export default CreateQuestionPaper;
